import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./nav.scss";

class Nav extends Component {
	componentDidMount() {
        window.addEventListener("scroll", this.SetMobileMenuPosition);
		setTimeout(this.SetMobileMenuPosition)
    }
	openCloseMobileMenu = () => {
		const nav = document.getElementById("title").classList;
		if (nav.contains("activeMenu")) nav.remove("activeMenu");
		else nav.add("activeMenu");
	};

	SetMobileMenuPosition = () => {
		const nav = document.getElementById("title").getBoundingClientRect();

		let topPos = nav.top + nav.height / 2;
		let halfHeight = window.innerHeight / 2;

		if (topPos > halfHeight) this.refs.mobileDropdown.classList.add("up");
		else this.refs.mobileDropdown.classList.remove("up");
	};

	HomeScroll = () => {
		// window.scrollTo(0, window.innerHeight - 400);
		window.scrollTo(0, 0);
	};

	render() {
		this.HomeScroll()
		return (
			<nav id="title">
				<ul id="base">
					<Link to="/">
						<li>
							<div id="logo"></div>
						</li>
					</Link>
					<li>
						<Link to="/">
							<h1 id="siteName">Theophilus</h1>
						</Link>
					</li>
					<li className="gap"></li>

					<ul id="lv1">
						<Link to="">
							<li className="btn">Home</li>
						</Link>
						<Link to="/about">
							<li className="btn">About Me</li>
						</Link>
						<li className="btn">
							Projects
							<ul className="lv2" ref="mobileDropdown">
								<Link to="/projects#web">
									<li className="btn">Web</li>
								</Link>
								<Link to="/projects#logo">
									<li className="btn">Logos and Banners</li>
								</Link>
								<Link to="/projects#model">
									<li className="btn">3D</li>
								</Link>
								<Link to="/projects#animation">
									<li className="btn">Animation</li>
								</Link>
								<Link to="/projects#others">
									<li className="btn">Electronics</li>
								</Link>
							</ul>
						</li>
						<Link to="/contact">
							<li className="btn">Contact</li>
						</Link>
					</ul>

					<li>
						<div
							onClick={this.openCloseMobileMenu}
							className="btn"
							id="mobileMenuButton"
						></div>
					</li>
				</ul>
			</nav>
		);
	}
}

export default Nav;
