import React, { Component } from 'react';
import "./rating.scss"

class RatingUnit extends Component{
    render(){
        return(
            <li className="rating" >
                {/*  <li [ngClass]="{
                        'q1':q1,
                        'q2':q2,
                        'q3':q3,
                        'q4':q4
                    }"> */}
                    {/* <div className="quad qm" [ngStyle]="mStyle"></div> */}
                    <div className="quad qm" ></div>
                    <div className="quad q1"></div>
                    <div className="quad q2"></div>
                    <div className="quad q3"></div>
                    <div className="quad q4"></div>

                    <p>{this.props.name}</p>
                    <p className="percent"><span className="val">{this.props.value}</span>%</p>
                </li>
        )
    }
}

class Rating extends Component{
    render(){
        return(
            <div className="rating">
                <h3>{this.props.name}</h3>
                <hr />
                <ul>
                    {this.props.data.map(inp => <RatingUnit key={inp[0]} name={inp[0]} value={inp[1]}/> )}
                </ul>
            </div>
        )
    }
}

export default Rating