import React, { Component } from "react";
import Rating from "../component/rating";

import "./about.scss";

class About extends Component {
	abilities = [
		[
			"Skills",
			[
				["C#", 82],
				["ASP.NET", 86],
				["Python", 90],
				["HTML", 95],
				["CSS", 90],
				["JavaScript", 65],
				["JQuery", 80],
				["PHP", 47],
				["SQL", 73],
				["Arduino", 92],
				["AIML", 83],
				["Cordova", 70]
			]
		],

		[
			"Tools",
			[
				["Windows", 96],
				["Microsoft Word", 84],
				["Excel", 63],
				["Power Point", 73],
				["CorelDRAW", 47],
				["Photoshop", 79],
				["Illustrator", 85],
				["InDesign", 68],
				["After Effects", 57],
				["Preniere Pro", 83],
				["Maya", 88],
				["3D Max", 73],
				["Motion Builder", 80],
				["AutoCAD", 37],
				["Blender", 80],
				["ZBrush", 67],
				["Unity3D", 91]
			]
		]
	];

	page = {
		title: "About",
		description: "This is the about me page"
	};

	componentDidMount() {
		this.props.pageProperties(this.page);
	}

	render() {
		return (
			<div className="grp">
				<div className="unit">
					<img src="/Images/site/Theophilus.jpg" alt="Theophilus" />
					My name is <strong>Theophilus Ogbolu</strong>, and this is
					the "About Me" stuff...
					<br />
					<br />
					I am a self-taught student of programming and software
					development, and I spend way too much time and money
					pursuing this interest. My friends and family are concerned
					for my well-being.
					<br />
					<br />
					In August of 2013 I accepted a position as a professional
					developer with a startup in Lagos, Nigeria. So I get paid to
					do what I would have been doing anyway, for free.
					<br />
					<br />
					I spend most of my free time working on building my
					skillset, contributing to Open Source projects, and trying
					to learn as much as I can. There have been many good people
					who have assited me in learning this craft, and I try to pay
					that forward by helping others who may be newer than I.
					<br />
					<br />
					I have benefited in massive ways from the work of those who
					took the time to share their know-how on the internet. From
					various bloggers to programming forums, to the now
					ubiquitous Stack Overow, I have, in Borg-like fashion,
					assimilated the experience and college education of some of
					the biggest (and not so big) names in the technology arena.
					Shamelessly!
					<br />
					<br />
					When I’m not writing code or designing, I tend to be playing
					music (my other passion), exploring my surroundings, and
					doing my best to experience life beyond the code editor.
					<br />
					<br />
				</div>

				<div className="unit">
					{this.abilities.map(abl => (
						<Rating key={abl[0]} name={abl[0]} data={abl[1]} />
					))}
				</div>
			</div>
		);
	}
}

export default About;
