import React, { Component } from "react";

class Home extends Component {
	componentDidMount() {
		this.props.pageProperties(this.page);
		document.getElementById("root").classList.add("home");
		document.getElementById("header").classList.add("home");
	}

	componentWillUnmount() {
		document.getElementById("root").classList.remove("home");
		document.getElementById("header").classList.remove("home");
	}

	page = {
		title: "Home",
		description: "Welcome!"
	};

	render() {
		return (
			<React.Fragment>
				<div className="unit">
					<h4>
						<strong>Hi.</strong>
					</h4>
					Welcome to my personal portfolio website.
					<br />I am a web developer / graphics designer based in
					Lagos. I have a passion for designing and I love to create
					for web and mobile devices.
				</div>
				<br />
				<br />
				<div className="unit left">
					<h4>
						<strong>What I can do.</strong>
					</h4>
					<br />
					<strong>Design what you want.</strong>
					<br />
					I like to keep it simple. My goals are focused on details,
					content and conveying the message that you want to send.
					<br />
					<br />
					<strong>Develop what you need.</strong>
					<br />I am a developer, so I kn ow how to create your
					website to run accross devices using the latest technologies
					available.
				</div>
				<br />
				<br />

				<div className="unit right">
					<h4>
						<strong>I can help.</strong>
					</h4>
					I am currently available for freelance work. If you have a
					project that you want to get started, think you need my help
					with something or just fancy saying hey, then get in touch.
				</div>
			</React.Fragment>
		);
	}
}

export default Home;
